.App {
  text-align: center;

}


.boxcontainer{
  padding: 0;
  margin: 0;
  width: 300px;
  height: 300px;
  border : 3px solid #52B3D9;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.box{
  width: 98px;
  height: 98px;
  border: 1px solid #52B3D9;
  padding: 0;
  margin: 0;
  font-size: 60px;
  cursor: pointer;
}

 .modal-dialog {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%) !important;
            -ms-transform: translate(-50%, -50%) !important;
                transform: translate(-50%, -50%) !important;
    }

#resetBtn{
  margin-top: 10px;
  margin-bottom: 10px;
}

#toggleDiv{
  margin-top : 20px;
  margin-bottom: 20px;
}

#xButton{
  margin-right: 10px;
  margin-left: 10px;
}







